import React, { useState } from "react";
import ChatWindow from "../../components/ChatWindow";
import { Button, Container, Typography, Box } from '@mui/material';

export default function ChatBot() {
    const [showChat, setShowChat] = useState(false); // false: 자소서 선택 화면, true: ChatWindow 화면

    return (
        <Container maxWidth={showChat ? "md" : "sm"} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '90vh' }}>
            {!showChat ? (
                <>
                    <Typography variant="h4" style={{ marginBottom: '20px' }}>자소서 선택</Typography>
                    <Typography variant="h6" style={{ marginBottom: '20px' }}>원하는 자소서를 선택.</Typography>
                    <Button
                        fullWidth
                        variant="contained"
                        color="primary"
                        onClick={() => setShowChat(true)}
                    >
                        다음
                    </Button>
                </>
            ) : (
                <Box display="flex" flexDirection="row" alignItems="center" width="100%" justifyContent="space-between">
                    <Typography
                        variant="subtitle1"
                        style={{ marginLeft: '20px', whiteSpace: 'pre-line' }}
                    >
                        {"선택한 자소서를 참고해\n예상 면접 질문을 해보세요!"}
                    </Typography>
                    <ChatWindow />
                </Box>
            )}
        </Container>
    );
}
